body {
    margin: 0;
    font-family: 'Inter', sans-serif;
    background-color: #010D23;
  }
  
  .login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .login-box {
    background: rgba(255, 255, 255, 0.1);
    padding: 40px;
    border-radius: 10px;
    text-align: center;
    width: 300px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .logo {
    width: 100px;
    margin-bottom: 20px;
  }
  
  .title {
    color: #FFFFFF;
    margin-bottom: 20px;
  }
  
  .input-container {
    position: relative;
    margin-bottom: 20px;
  }
  
  .input {
    width: 100%;
    padding: 10px;
    padding-left: 40px;
    background: rgba(255, 255, 255, 0.5);
    border: none;
    border-radius: 5px;
    color: #FFFFFF;
  }
  
  .input::placeholder {
    color: rgba(255, 255, 255, 0.7);
  }
  
  .icon-email, .icon-password {
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
    color: rgba(255, 255, 255, 0.7);
  }
  
  .sign-in-button {
    width: 100%;
    padding: 10px;
    background-color: #038BBB;
    border: none;
    border-radius: 5px;
    color: #000000;
    font-weight: bold;
    cursor: pointer;
  }
  
  .google-sign-in {
    margin: 20px 0;
  }
  
  .google-sign-in img {
    width: 24px;
    cursor: pointer;
  }
  
  .register-prompt {
    color: #FFFFFF;
  }
  
  .register-link {
    color: #E19F41;
    text-decoration: none;
  }
  
  .register-link:hover {
    text-decoration: underline;
  }
  